  }
  .-shrink-none\@lg {
    flex-shrink: 0;
  }
  .-grow-single\@lg {
    flex-grow: 1;
  }
  .-shrink-single\@lg {
    flex-shrink: 1;
  }
  .-grow-double\@lg {
    flex-grow: 2;
  }
  .-shrink-double\@lg {
    flex-shrink: 2;
  }
  .-dir-row\@lg {
    flex-direction: row;
  }
  .-dir-col\@lg {
    flex-direction: column;
  }
  .-dir-row-reverse\@lg {
    flex-direction: row-reverse;
  }
  .-dir-col-reverse\@lg {
    flex-direction: column-reverse;
  }
  .h-text-left\@lg {
    text-align: left;
  }
  .h-text-center\@lg {
    text-align: center;
  }
  .h-text-right\@lg {
    text-align: right;
  }
  .h-text-justify\@lg {
    text-align: justify;
  }
}
.row.-fill > .col > *,
.col.-fill > * {
  flex-grow: 1;
}
input:focus,
button:focus,
textarea:focus,
input:focus:active,
button:focus:active,
textarea:focus:active {
  outline: none;
}
